import React, { useState } from "react";
import InputComponent from "./InputComponent";

const LoginForm = () => {
	const [account, setAccount] = useState({ userName: "", password: "" });
	const [errors, setErrors] = useState({});

	const validate = () => {
		const errors = {};

		if (account.userName.trim() === "") {
			errors.userName = "Username is required!";
		}
		if (account.password.trim() === "") {
			errors.password = "Password is required!";
		}

		return Object.keys(errors).length === 0 ? null : errors;
	};

	const handleSubmit = e => {
		e.preventDefault();

		const errorsValue = validate();
		setErrors(errorsValue || {});
		if (errorsValue) return;
	};

	const validateProperty = ({ name, value }) => {
		if (name === "userName") {
			if (value.trim() === "") return "Username is required.";
		}
		if (name === "password") {
			if (value.trim() === "") return "Password is required.";
		}
	};

	const handleChange = ({ currentTarget: input }) => {
		const errorsInput = { ...errors };
		const errorMessage = validateProperty(input);
		if (errorMessage) errorsInput[input.name] = errorMessage;
		else delete errorsInput[input.name];

		setErrors(errorsInput);

		const accountNew = { ...account };
		accountNew[input.name] = input.value;
		setAccount(accountNew);
	};

	return (
		<div>
			<h1>Login</h1>
			<form onSubmit={handleSubmit}>
				<InputComponent
					value={account.userName}
					name='userName'
					label='Username'
					handleChange={handleChange}
					error={errors.userName}
				/>
				<InputComponent
					value={account.password}
					name='password'
					label='Password'
					handleChange={handleChange}
					error={errors.password}
				/>
				<button className='btn btn-primary'>Submit</button>
			</form>
		</div>
	);
};

export default LoginForm;
